/* Purchase Window */

.modal-background {
	position: fixed;
	top: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.8);
	z-index: 10000;
}
.modal-window {
	position: relative;
	width: 90%;
	max-width: 380px;
	min-height: 300px;
	max-height: 80vh;
	margin: 2%;
	padding: 16px;
	background-color: #dedede;
	border-radius: 8px;
	text-align: center;
	color: #000;
	overflow-y: auto;
}
.modal-window .cross {
	position: absolute;
	top: -4px;
	right: 6px;
	font-size: 24px;
	cursor: pointer;
}
.modal-window h3 {
	margin-bottom: 12px;
}
.modal-window .first-step .movie-data {
	box-sizing: border-box;
	padding: 4px;
	border-top: 2px solid #999;
	border-bottom: 2px solid #999;
}
.modal-window .first-step .movie-data p:first-of-type {
	margin: 4px 0;
	font-weight: bold;
	color: #000;
}
.modal-window .first-step .movie-data p:last-of-type {
	margin: 4px 0;
	font-size: 0.9em;
	color: #444;
}
.modal-window .first-step .tickets table {
	width: 100%;
}
.modal-window .first-step .buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 40px;
}
.modal-window .first-step .buttons span {
	padding: 4px;
	font-weight: bold;
	color: #880808;
}
.modal-window .second-step p {
	margin-top: 4px;
	text-align: center;
	font-size: 0.9em;
	color: #777;
}
.modal-window .second-step .payment-methods {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.modal-window .second-step .payment-methods img {
	flex: 1;
	width: 45%;
	max-width: 157px;
	margin: 4px;
	border: 2px solid #e0e0e0;
	border-radius: 8px;
	background: linear-gradient(145deg, #f7f7f7 0%, #b5b5b5 100%);
	cursor: pointer;
}
.modal-window .second-step .payment-methods .active {
	border-color: #4169e1;
}
.modal-window .second-step .buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 4px;
}
.modal-window .buttons button {
	background-color: transparent;
	text-transform: uppercase;
	border: none;
	outline: none;
	cursor: pointer;
}
.modal-window .buttons button:first-of-type {
	font-size: 0.8em;
}
.modal-window .second-step .buttons button:last-of-type {
	padding: 4px;
	font-weight: bold;
	color: #000;
}

/* Server Response Window */

.modal-background.lite {
	background-color: transparent;
	left: 0;
}
.response-window {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 20px;
	border-radius: 10px;
	box-shadow: 0 0 3px 1px #000;
}
.success {
	background-color: #03b13d;
}
.danger {
	background-color: #fc5235;
}
.response-window p {
	text-align: center;
	font-size: 1.1em;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.response-window p span {
	font-size: 1.5em;
	font-weight: bold;
}
.response-window.success p span {
	color: #046d27;
}
.response-window.danger p span {
	color: #d62303;
}
