.Ticket {
	display: flex;
	position: relative;
	cursor: pointer;
	opacity: 0.8;
}
.Ticket:hover .shadow {
	opacity: 0.9;
}
.Ticket .shadow,
.Ticket.expired .shadow {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	color: #fff;
	font-size: 1.2em;
	text-transform: uppercase;
	background-color: #000;
	border-radius: 3px;
	opacity: 0;
	transition: 0.3s;
	z-index: 100;
}
.Ticket.expired .shadow {
	top: 8px;
	left: 10px;
	width: calc(100% - 20px);
	height: calc(100% - 16px);
	opacity: 0.9;
	transition: 0.5s;
}
.Ticket.expired .shadow:hover {
	opacity: 0;
}
.Ticket.expired .shadow div {
	padding: 6px 10px;
	color: #b10606;
	background-color: transparent;
	border: 3px solid #b10606;
	border-radius: 8px;
	transform: rotate(-27deg);
}
.Ticket .data-block {
	display: flex;
	flex-direction: column;
	width: 100%;
	box-sizing: border-box;
	margin: 8px 10px;
	padding: 10px;
	background-color: #cecece;
	box-shadow: 0px 0px 4px 1px #000 inset;
}
.Ticket .data-block > div {
	display: flex;
}
.Ticket .data-block p {
	margin: 8px;
}
.Ticket .data-block .main-data {
	justify-content: space-around;
	font-size: 16px;
	font-weight: bold;
}
.Ticket .data-block .main-data p {
	width: 50%;
}
.Ticket .data-block .main-data p:last-of-type {
	font-size: 14px;
	font-weight: normal;
}
.Ticket .data-block .seat-data {
	justify-content: center;
	font-weight: bold;
}
.Ticket .data-block .summary-data {
	justify-content: space-between;
}
.Ticket .data-block .summary-data p:first-of-type {
	font-size: 16px;
	font-weight: bold;
	color: #8d0202;
	text-decoration: underline;
}
.Ticket .buttons {
	display: flex;
	justify-content: center;
	align-items: center;
}
.Ticket .buttons button {
	width: 70%;
	height: 70%;
	padding: 16px;
	font-size: 14px;
	border: none;
	color: #fff;
	background-color: #000;
	box-shadow: 0px 0px 3px 1px #999 inset;
	outline: none;
	cursor: pointer;
}
