body {
	margin: 0;
	padding: 0;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	overflow-x: hidden;
	background-color: #0f0f0f;
}
#root {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
.main {
	flex-grow: 1;
}

footer {
    flex-shrink: 0;
    text-align: center;
    color: #bbb;
    background-color: #0A0A0A;
}
footer div {
    padding: 25px 25px 0;
}
footer div p {
    margin: 16px;
}
footer>p {
    margin: 50px 0 5px 0;
    text-align: center;
    font-size: 0.9em;
    font-style: italic;
    color: #888;
}
.Loader-box {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #0f0f0f;
}
.Loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.Loader div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #FF6D12;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #FF6D12 transparent transparent transparent;
}
.Loader div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.Loader div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.Loader div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
#noop {
	width: 100%;
	height: 61px;
	background-color: #000;
}
.navbar {
	position: fixed;
	width: 100%;
	background-color: #000;
	overflow: hidden;
	z-index: 1000;
	transition: 0.3s;
}
.navbar span {
	color: #ff6d12;
	font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
	font-size: 24px;
	cursor: default;
}

.navbar ul {
	margin: 0;
	padding: 0;
	text-transform: uppercase;
}
.navbar ul:last-of-type {
	float: right;
}

.navbar li,
.navbar span {
	float: left;
	display: block;
	padding: 16px;
	margin: 0px 10px;
}
.navbar a {
	color: #fafafa;
	text-align: center;
	text-decoration: none;
	font-size: 17px;
}
.navbar li,
.navbar a {
	transition: 0.2s;
}

.navbar li:hover {
	background-color: #ddd;
}
.navbar li:hover a {
	color: #000;
	text-decoration: underline;
}
.navbar .i {
	display: flex;
	flex-direction: row;
	align-items: center;
	position: absolute;
	top: 14px;
	right: 20px;
	display: none;
}
.navbar .i div {
	width: 6px;
	height: 6px;
	margin: 4px;
	border-radius: 50%;
	background-color: #fff;
}

@media screen and (min-width: 900px) {
	.navbar {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.navbar-wrapper {
		display: flex;
		align-items: center;
		justify-content: left;
	}
}

@media screen and (max-width: 900px) {
	.navbar li {
		display: none;
	}
	.navbar .i {
		display: block;
	}
}

@media screen and (max-width: 900px) {
	.navbar.responsive {
		top: 0px;
		width: 100%;
		padding-bottom: 10px;
		z-index: 1000;
	}
	.navbar.responsive .i div {
		width: 34px;
		height: 4px;
		border-radius: 4px;
	}
	.navbar.responsive .i div:nth-child(1) {
		transform: rotate(45deg);
		position: relative;
		top: 11px;
		left: 14px;
	}
	.navbar.responsive .i div:nth-child(2) {
		transform: rotate(-45deg);
		position: relative;
		top: 3px;
		left: 14px;
	}
	.navbar.responsive .i div:last-child {
		display: none;
	}
	.navbar.responsive li {
		float: none;
		display: block;
		text-align: center;
		padding-top: 14px;
	}
	.navbar.responsive .navbar-wrapper {
		margin-top: 52px;
	}
	.navbar.responsive ul {
		float: none;
	}
	.navbar.responsive span {
		display: none;
	}
}

