.auth-page {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	background-color: #0f0f0f;
}
.auth-block {
	width: 480px;
	margin: 40px 0 50px;
}
.auth-block > p {
	margin-bottom: 30px;
	text-align: center;
	font-size: 1.1em;
	color: #fff;
}
.auth-block .error-message {
	margin: 0;
	font-size: 1em;
	color: #bb0000;
}
.auth-buttons {
	display: flex;
	justify-content: space-between;
	width: 100%;
}
.auth-buttons p {
	padding-left: 4px;
	color: #fff;
	font-size: 0.9em;
}
.auth-buttons a {
	color: #999;
	text-decoration: none;
}
.auth-buttons a:hover {
