}
.contact-page .contact-form textarea:focus,
.contact-page .contact-form textarea:hover {
	background-color: transparent;
	box-shadow: 0 0 4px 1px #ff6d12 inset;
}
.contact-page .contact-form textarea:focus::-webkit-input-placeholder {
	color: #fff;
}
.contact-page .contact-form textarea:focus:-ms-input-placeholder {
	color: #fff;
}
.contact-page .contact-form textarea:focus::-ms-input-placeholder {
	color: #fff;
}
.contact-page .contact-form textarea:focus::placeholder {
	color: #fff;
}
.contact-page .contact-form textarea::-webkit-scrollbar {
	background: transparent;
	width: 10px;
}
.contact-page .contact-form textarea::-webkit-scrollbar-button {
	display: none;
}
.contact-page .contact-form textarea::-webkit-scrollbar-thumb {
	background: linear-gradient(
		to bottom,
		transparent 20%,
		#ff6d12 50%,
		transparent 80%
	) !important;
}
.contact-page .contact-form textarea::-webkit-scrollbar-track {
	background: transparent;
}
.contact-page .contact-form textarea::-webkit-scrollbar-track-piece {
	background: transparent;
}
.contact-page .contact-form textarea::-webkit-scrollbar-corner {
	display: none;
}
.contact-page .contact-form textarea::-webkit-resizer {
	display: none;
}

.contact-page .contact-form > div:nth-of-type(3) {
	width: 100%;
	height: 50px;
	margin-top: 25px;
	text-align: center;
}
.contact-page .contact-form .submit button {
	padding: 8px 14px;
	font-size: 1.2em;
	text-transform: uppercase;
	color: #fff;
	background-color: transparent;
	border: none;
	outline: none;
	transition: 0.3s;
	cursor: pointer;
}
.contact-page .contact-form .submit button:hover {
	color: #ff6d12;
	border-bottom: 3px solid #ff6d12;
}
.contact-page .contact-form .submit button:disabled {
	color: #999;
	cursor: not-allowed;
}
.contact-page .contact-form .submit button:disabled:hover {
	color: rgba(145, 1, 1, 0.7);
	border-bottom: none;
}
.contact-page .map {
	order: 2;
	box-sizing: border-box;
	width: 60%;
	height: auto;
	margin: 25px 5px;
}

@media (max-width: 900px) {
	.contact-page .contact-info {
		order: 1;
		width: 100%;
