.calendar {
	display: flex;
	justify-content: center;
	width: 80%;
	margin: auto;
	margin-bottom: 20px;
}
.calendar > div {
	margin-top: 20px;
	text-align: center;
}
.switcher {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	width: 11%;
	max-width: 60px;
	background-color: #000;
	color: #000;
	text-transform: uppercase;
	font-size: 14px;
	cursor: pointer;
	transition: 0.4s;
}
.switcher:hover {
	color: #fff;
}
.switcher:first-of-type {
	padding-left: 3px;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	border-right: 2px solid #ff6d12;
}
.switcher:last-of-type {
	padding-right: 3px;
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%;
	border-left: 2px solid #ff6d12;
}

.day {
	box-sizing: border-box;
	width: 28%;
	padding: 10px 20px;
	border: #000 1px solid;
	background-color: #000;
	color: #fff;
	transition: box-shadow 0.4s;
}
.day:hover {
	cursor: pointer;
	box-shadow: 0px 15px 15px -10px #ff6d12;
}
.day p {
	padding: 0;
}
.day p:first-child {
	font-size: 1em;
	margin: 0;
}
.day p:last-child {
	font-size: 0.7em;
	margin: 0;
}
.selected {
	color: #000;
	background-color: #ff6d12;
	border-bottom: none;
	box-shadow: 0px 0px 20px 2px #000 inset;
}
.day.selected:hover {
	cursor: default;
	color: #000;
	background-color: #ff6d12;
	box-shadow: 0px 0px 20px 2px #000 inset;
}

.movie {
	display: flex;
	max-width: 540px;
	margin: 40px 15% 0;
	background-color: #000;
	color: #fff;
