.Input label {
	margin-bottom: 3px;
	padding-left: 3px;
	font-size: 1em;
	color: #fff;
}
.Input input {
	box-sizing: border-box;
	width: 100%;
	margin-bottom: 20px;
	padding: 8px 16px;
	font-size: 1em;
	border: 3px solid black;
	outline: none;
}
.Input input::placeholder {
	font-size: 0.9em;
}
.Input.warning label {
	color: #e60000;
}
.Input.warning input {
	border-color: #e60000;
}
